import React, { useContext } from 'react'
import { FileCtx } from '../../../ctx/file-ctx'
import styles from '../NavBar.module.css'
type Props = {
    home:boolean
}

const HomeBtn = (props: Props) => {
const {setFile,setPortList,setSelectedFiles,setSelectedSender}=useContext(FileCtx)


//resets the current file causing return to choose file
const clickHomeHandler=(e:any)=>{
    setFile(null)
    setPortList(null)
    setSelectedFiles(null)
    setSelectedSender('504')
    window.history.replaceState({}, document.title, "/")
    }

//resets the current file causing return to choose file
const clickBackHandler=(e:any)=>{
    setPortList(null)
    window.history.replaceState({}, document.title, "/")
    }

  return (
    <button onClick={props.home?clickHomeHandler:clickBackHandler} className={styles.home_btn} ><img className={styles.home} alt='navBtn' src={props.home?'/home.png':"/back.png"}></img></button>
  )
}

export default HomeBtn