import React,{useState} from 'react';
import {FileCtx} from './ctx/file-ctx'
import './App.css';
import Dash from './pages/Dash';

import { useFileInit } from './hooks/useFileInit';
import HomePage from './pages/HomePage';
import { formatDate } from './util/dateFormat';
import LoadingSpinner from './components/UI/LoadingSpinner';

export default function App() {

  
  const [file,setFile]=useState<any>(null)
  const [selectedFiles,setSelectedFiles]=useState<any>(null)
  const [isLoading,setIsLoading]=useState<any>(false)
  const [selectedSender,setSelectedSender]=useState<any>("504")
 const {portList,portArray,protocolArray,senderArray,setPortList}=useFileInit(file)
 const [fileNameArray,setFileNameArray]=useState<any>(null) 



 const fileArrayHandler=(fileArray:any)=>{
  if(!fileArray.length){
    const lastDate=new Date(fileArray.date)
    const name = fileArray.filename +' ( '+formatDate(lastDate)+' )'
  setFileNameArray(name)
  
  }else if (fileArray.length>0&&fileArray.length<5){
   let fileBase=fileArray[0].filename.split(/[-.]/)[0] 
   let fileNum:Array<string>=[];
   const lastDate=new Date(fileArray[0].date)
   fileArray.map((file:any)=>{
  return fileNum.push(file.filename.split(/[-.]/)[1])
   }) 
   setFileNameArray(fileBase+' ('+fileNum+') '+ formatDate(lastDate));
  }else{
    let fileBase=fileArray[0].filename.split(/[-.]/)[0] 
    let fileNum:Array<string>=[];
    const lastDate=new Date(fileArray[0].date)
    fileArray.map((file:any)=>{
   return fileNum.push(file.filename.split(/[-.]/)[1])
    }) 
    setFileNameArray(`${fileBase} ( ${fileNum.slice(0,5)} +${fileNum.length-5} more) ${formatDate(lastDate)}`);
    
  }
  }
 
    return (
      <FileCtx.Provider
      value={{
        selectedFiles,setSelectedFiles,
        file,
        setFile,
        portList,
        setPortList,
        protocolArray,
        senderArray,
        portArray,
selectedSender,
setSelectedSender,
        isLoading,
        setIsLoading
      }}
    >
      {isLoading&& <LoadingSpinner/>}
     {!portList&& <HomePage fileArray={fileArrayHandler} />}
       { file&&protocolArray.length&&portList&& <Dash fileNameArray={fileNameArray}  />  }
       <div className='ver'>Flow Analyzer v1.2.43(web)</div>
      </FileCtx.Provider>
    );
  }