import { Popover, Typography } from '@mui/material'
import { DataGrid, GridColumns } from '@mui/x-data-grid'

import React,{useState,useContext} from 'react'
import { FileCtx } from '../../../ctx/file-ctx';
import { useHttp } from '../../../hooks/http-hook';
import { protocolIndex } from '../../../util/ipProtocols';
import wellKnownPorts from '../../../util/wellKnownPorts.json'


interface tableRow {
    id: string; CMD: string; key: string; count:number;
    dstIp:string; dstPort:string; hostDst:string; hostSrc:string; ipProtocol:string;
    ipSender:string;ipSize:number; outputPort1:string
   srcPort:string; value:number;
    srcIp:string;packets:number; outputPort2:string;
    ip:string;
}

type Props = {
    columns:GridColumns,
    data:Array<tableRow>
}


const Table = (props: Props) => {
  const {sendRequest}=useHttp()
    const [anchorEl, setAnchorEl] = useState<HTMLElement | null>(null);
    const{portList}=useContext(FileCtx)
    const [value, setValue] = useState("");
  

    const handlePopoverOpen =(event: React.MouseEvent<HTMLElement>) => {
      const field = event.currentTarget.dataset.field!;
      const id = event.currentTarget.parentElement!.dataset.id!;
      const row = props.data.find((r) => r.id === id)!;
      const text=event.currentTarget.textContent!;
      
      if(field==='dstIp'||field==='srcIp'||field==='ip'){
        const host=window.location.hostname
     sendRequest(`http://${host}:8087/flow-analyzer/api/dns`,'POST', JSON.stringify({
          ipHost: row[field],
        }),
        {
          "Content-Type": "application/json",
        }).then((value)=>{
          const {host}=value
             if(host.length>0){
                 setValue(row[field]+' '+host)  
             }
             else{
                 setValue(text)
             }
            });
       }else if (field==='ipProtocol'|| field==='protocol') {
       setValue(protocolIndex[parseInt(row[field])][0])
       }else if ((field==='outputPort1'||field==='outputPort2')&&portList[row['ipSender']]) {
        let trimmedPort;
        if(row[field].indexOf('00')=== 0){
          trimmedPort=row[field].replace('00',"")
        }else{
          trimmedPort=row[field]
        }
       const portDescription=portList[row['ipSender']].data.filter((row:Array<string>)=>{
      return row[1]===trimmedPort
        })
        
        if(portDescription.length){
           const portArr=portDescription[0]
          const description=portArr.slice(7).join("").replaceAll("-",'')
          const ip=portArr[6].replaceAll("-_ -_",'') 
          setValue(`${text} ${ip} ( ${description} )`)
         
        }
        else{
          setValue(text)
        }
      }else if((field==='srcPort'||field==='dstPort') && wellKnownPorts[row[field]]){  
      setValue (wellKnownPorts[row[field]][0].description)
         }else{  
       setValue(text);
       }
       setAnchorEl(event.currentTarget);
     };
  

    const handlePopoverClose = () => {
      setAnchorEl(null);
    };
   
    const open = Boolean(anchorEl);


  return (
    <React.Fragment>
<DataGrid  onCellFocusOut={handlePopoverClose} onColumnHeaderLeave={handlePopoverClose}  columns={props.columns} rows={props.data} getRowId={(row)=>(row.id??row.key)}  componentsProps={{
          cell: {
            onMouseEnter:handlePopoverOpen,
            onMouseLeave:handlePopoverClose,
            },}}/>
<Popover
        sx={{
          pointerEvents: 'none',
        }}
        open={open}
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        onClose={handlePopoverClose}
        disableRestoreFocus
      >
     <Typography sx={{ p: 2 }}>{value}</Typography>
     
      </Popover>


    </React.Fragment>
  )
}

export default Table