import { Box, Button, Fade, FormControl, InputLabel, MenuItem, Modal, Paper, Select, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material'
import React, { Fragment ,useContext,useEffect,useState} from 'react'
import { FileCtx } from '../../ctx/file-ctx';
import { formatDate } from '../../util/dateFormat';

type Props = {sender:string}
const IntfModal = (props: Props) => {
    const [open, setOpen] = useState(false);
    const [tableRows, setTableRows] = useState<undefined|Array<any>>();
    
    const [curSender, setCurSender] = useState<string>();
    const [tableHeader, setTableHeader] = useState<string>();
    const {portList,selectedSender}=useContext(FileCtx)

useEffect(()=>{
  if(!curSender){
    setCurSender(props.sender)
  }
},[props,curSender])

    const handleChange = (e:any) =>{
      e.preventDefault()
      setCurSender(e.target.value)
      }

        const handleOpen = (e:any) =>{
          e.preventDefault()
          if(curSender!=='504'){
            setTableRows(portList![curSender].data)
            setTableHeader(portList![curSender].filename + formatDate(portList![curSender].creationDate))
          }
         else{ setTableRows(portList![selectedSender].data)
          setTableHeader(portList![selectedSender].filename +" "+ formatDate(new Date(portList![selectedSender].creationDate)))}
           setOpen(true)
        }
        
    const handleClose = () => setOpen(false);
    const style = {
        position: 'absolute' as 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        bgcolor: 'background.paper',
        border: '2px solid transperent',
        boxShadow: 24,
        p: 2,
        width:'95%'
      };
   

  return (
  <Fragment>
 <form onSubmit={handleOpen} style={{display:'flex' }}>
<FormControl sx={{marginRight:1 }}>
 <InputLabel sx={{color:'rgba(57, 101, 125, 0.8274509804)'}} htmlFor="sender">Senders</InputLabel>
 <Select sx={{height:40 ,width:'max-content' , backgroundColor:'white',color:'rgba(57, 101, 125, 0.8274509804)'}} defaultValue={selectedSender} label="Sender" onChange={handleChange} id="sender" name="sender">
{portList&& Object.keys(portList).map(sender => {
    return  <MenuItem sx={{color:'rgba(57, 101, 125, 0.8274509804)'}} value={sender} id={sender} key={sender} >{sender +` (${portList[sender].senderName})`}</MenuItem>
    })  }
</Select>
</FormControl>
    <Button sx={{  backgroundColor:'white', color:"#39657dd3",fontSize:'1.3rem',borderRadius:'10px' ,textTransform:'capitalize' ,minWidth:'max-content',height:'40px',marginRight:'10px' ,  border:"2px solid rgba(57, 101, 125, 0.8274509804)" }} type='submit'>View Sender Interfaces</Button>
    </form>
<Modal
  open={open}
  onClose={handleClose}
  aria-labelledby="modal-modal-title"
  aria-describedby="modal-modal-description"
>
    
<Fade in={open}>
  <Box sx={style} >
    <Paper sx={{ width: '100%', overflow: 'hidden' }}>
  <TableContainer sx={{width:'100%',minWidth:600,maxHeight: 500 }} component={Paper}  >
      <Table stickyHeader aria-label="sticky table" sx={{width:'100%' ,maxHeight: 440 } } size="medium" >
      <TableHead>
           <TableRow >
            <TableCell>
           {tableHeader}
           </TableCell>
           </TableRow>
          </TableHead>
        <TableBody>
          {tableRows&& tableRows.map((row:Array<string>,i) => (
            <TableRow
              key={row[0]}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell key={row[i]} component="th" scope="row">
                {row[0]}
              </TableCell>
              {row.map( (cell,i)=>(i!==0 && <TableCell key={cell+i} sx={{flex:1}} align="left">{cell}</TableCell>)) }
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </Paper>
  </Box>
  </Fade>
</Modal>

</Fragment>
  )
}

export default IntfModal