import { formatDate } from "./dateFormat";

let hostDst = "dstIp";
let hostSrc = "srcIp";

function valueCheck(params) {
  hostSrc = params.row.hostSrc ? "hostSrc" : "srcIp";
  return params.row.hostSrc ?? params.row.srcIp;
}

function valueDst(params) {
  hostDst = params.row.hostDst ? "hostDst" : "dstIp";
  return params.row.hostDst ?? params.row.dstIp;
}

function dateMaker(params) {
  const date = new Date(params.row.date);
  return formatDate(date);
}

export function createColumns(isSflow) {
  return {
    columns: [
      { field: "id", headerName: "id", width: 30 },
      { field: "ipSender", headerName: "Sender", flex: 0.8 },
      {
        field: "srcIp",
        headerName: "Source Ip",
        valueGetter: valueCheck,
        flex: 1.4,
      },
      {
        headerName: "Destination Ip",
        field: "dstIp",
        valueGetter: valueDst,
        flex: 1.4,
      },
      { field: "outputPort1", headerName: "outputPort 1", flex: 0.5 },
      { field: "outputPort2", headerName: "outputPort 2", flex: 0.5 },
      { field: "ipProtocol", headerName: "Protocol", flex: 0.5 },
      {
        field: "packets",

        renderHeader: (params) => {
          return <div>{isSflow ? "Packets" : "In Byte"}</div>;
        },
        flex: 0.5,
      },
      {
        field: "ipSize",

        renderHeader: (params) => {
          return <div>{isSflow ? "Ip Size" : "Out Byte"}</div>;
        },

        flex: 0.5,
      },

      {
        field: "value",
        type: "number",
        headerName: "Packets %",
        cellClassName: (params) => {
          if (params.row.value > 30) {
            return "danger-pack";
          } else if (params.row.value > 20) {
            return "high-pack";
          } else if (params.row.value > 10) {
            return "medium-pack";
          } else {
            return "low-pack";
          }
        },
        flex: 0.5,
      },
    ],
    columnsAppTable: [
      { field: "id", headerName: "id", width: 30 },
      { field: "ipSender", headerName: "Sender", flex: 0.8 },
      {
        field: hostSrc,
        headerName: "Source Ip",
        valueGetter: valueCheck,
        flex: 1.2,
      },

      { field: "srcPort", headerName: "App srcPort", flex: 0.5 },
      {
        headerName: "Destination Ip",
        field: hostDst,
        valueGetter: valueDst,
        flex: 1.2,
      },
      { field: "dstPort", headerName: "App dstPort", flex: 0.5 },
      { field: "outputPort1", headerName: "outputPort 1", flex: 0.6 },
      { field: "outputPort2", headerName: "outputPort 2", flex: 0.6 },
      { field: "ipProtocol", headerName: "Protocol", flex: 0.5 },
      {
        field: "packets",

        renderHeader: (params) => {
          return <div>{isSflow ? "Packets" : "In Byte"}</div>;
        },
        flex: 0.5,
      },
      {
        field: "ipSize",
        renderHeader: (params) => {
          return <div>{isSflow ? "Ip Size" : "Out Byte"}</div>;
        },
        flex: 0.5,
      },

      {
        field: "value",
        type: "number",
        headerName: "Packets %",
        cellClassName: (params) => {
          if (params.row.value > 30) {
            return "danger-pack";
          } else if (params.row.value > 20) {
            return "high-pack";
          } else if (params.row.value > 10) {
            return "medium-pack";
          } else {
            return "low-pack";
          }
        },
        flex: 0.5,
      },
    ],
    topColumns: [
      { field: "id", headerName: "id", width: 50 },
      { field: "ipSender", headerName: "Sender", flex: 1 },
      { field: "ip", headerName: "Ip", flex: 1 },
      {
        field: "packets",
        renderHeader: (params) => {
          return <div>{isSflow ? "Packets" : "In Byte"}</div>;
        },
        flex: 1,
      },
      {
        field: "ipSize",
        renderHeader: (params) => {
          return <div>{isSflow ? "Ip Size" : "Out Byte"}</div>;
        },
        flex: 1,
      },

      { field: "protocol", headerName: "Protocol", flex: 1 },
      {
        field: "value",
        type: "number",
        headerName: "Packets %",
        cellClassName: (params) => {
          if (params.row.value > 30) {
            return "danger-pack";
          } else if (params.row.value > 20) {
            return "high-pack";
          } else if (params.row.value > 10) {
            return "medium-pack";
          } else {
            return "low-pack";
          }
        },
        flex: 1,
      },
    ],
  };
}

