import { Box, Dialog, Typography } from '@mui/material'
import React, { useContext, useEffect, useState } from 'react'
import HomeBtn from '../components/UI/btns/HomeBtn'
import SelectTable from '../components/UI/tables/SelectTable'
import { FileCtx } from '../ctx/file-ctx'
import { useHttp } from '../hooks/http-hook'

type Props = {
fileArray:(file:Array<object>)=>void
}

interface FileObj {
  filename:string,
  date:string,
  path:string,
  sender:string
  }

const HomePage = (props: Props) => {
 const [isIntf,setIsIntf] =useState(false)
 const [isSflow,setIsSflow] =useState(false)
 const [error,setError] =useState(null)
 const searchParams = new URLSearchParams(document.location.search)

const {setIsLoading,setFile,setSelectedSender,file,selectedFiles,setSelectedFiles}=useContext(FileCtx)
const{sendRequest}=useHttp()


useEffect(()=>{
if(file){
setIsIntf(true)
setIsSflow(selectedFiles[0].includes('sflow'))
}
},[file,selectedFiles])

useEffect(()=>{
  const urlSender=searchParams.get('sender')
  const urlHistory=searchParams.get('history')
  
  if(urlSender){
    const fetchAvailableFiles=async(urlSender,history)=>{
      const host=window.location.hostname
    const {portList} =await sendRequest(`http://${host}:8087/flow-analyzer/api/intf/`);
    let isSflowInit=false;
    if(portList){
      const reqSender:Array<FileObj>=[...portList.sflow,...portList.netflow].filter((port)=>port.sender==urlSender)
      isSflowInit=reqSender[0].path.includes('sflow')
      senderSelectHandler(reqSender)
    }
    const {fileList}:{fileList:Array<FileObj>} =await sendRequest(`http://${host}:8087/flow-analyzer/api/analyze/${isSflowInit}`);
    if(fileList){
    const reqFiles=fileList.sort((a,b)=> new Date(b.date).getTime() - new Date(a.date).getTime());
  console.log(reqFiles.filter((file,index)=> Number(history)>index));
  
      filesSelectHandler(reqFiles.filter((file,index)=> Number(history)>index))

    }
    
    }
  fetchAvailableFiles(urlSender,urlHistory)
  }
  
  },[])

  const senderSelectHandler=(selectedSender:Array<FileObj>)=>{ 
  setSelectedSender(selectedSender[0].sender)
  setIsSflow(selectedSender[0].path.includes('sflow'))
    setIsIntf(true)
  }

const filesSelectHandler=async(selectedfiles:Array<object>)=>{
  const host=window.location.hostname
const  selectedFileId=selectedfiles.map((file:FileObj)=>{
    return file.filename
  })
  setIsLoading(true)
  if(!file||selectedFiles.toString()!==selectedFileId.toString()){
    const {fileArr} =await sendRequest(`http://${host}:8087/flow-analyzer/api/analyze`,'POST',JSON.stringify({
      filesList: selectedfiles,
    }),
    {
      "Content-Type": "application/json",
    });
    if(fileArr.length===0){
      setIsLoading(false)
     setError('Files Selected Are Empty!')
      return
    }
    setFile(fileArr)
  }else if(selectedFiles.toString()===selectedFileId.toString()&&file){ 
   setFile([...file])
  }
  
   setSelectedFiles(selectedFileId)
  props.fileArray(selectedfiles)

  }
 
  return (
    <div className='file-input'>

      
    <a href="https://www.net-monitor.net" ><div className='logo'></div></a>
    {isIntf&&<div onClick={()=>setIsIntf(false)} style={{marginBottom:'5px'}}><HomeBtn home/></div>}

         {!isIntf&& <div className='form-container'><SelectTable senders fetchPath='intf/'  submitHandler={senderSelectHandler}/> </div> } 
         {isIntf&&  <div className='form-container'><SelectTable fetchPath={`analyze/${isSflow}`} selectedFiles={selectedFiles} submitHandler={filesSelectHandler}/> </div> } 
         <Dialog open={typeof error === 'string'} onClose={()=>{setError(null)}}><Box sx={{color:'#de3d37',padding:'5%'}}><h3 style={{margin:'5px',textDecoration:"underline"}}>Warning!</h3><Typography>{error}</Typography></Box></Dialog>
        </div>
  )
}

export default HomePage