import React, { useEffect, useState } from 'react'
import { DataGrid, GridSelectionModel, GRID_CHECKBOX_SELECTION_COL_DEF } from '@mui/x-data-grid'
import { useHttp } from '../../../hooks/http-hook'
import { selectSenderColumns,selectFileColumns } from '../../../util/tableColomns'



type Props = {
submitHandler:(file:Array<object>|string)=>void,
fetchPath:string,
senders?:boolean
selectedFiles?:Array<string>
}
interface FileObj {
filename:string,
date:string,
path:string,
sender:string
hostName:string
}

const columns=[{
  ...GRID_CHECKBOX_SELECTION_COL_DEF,
  renderHeader: (params) => (
    <strong style={{color:'blue'}} role="img" aria-label="enjoy">  
      ✔️
    </strong>
  ),

},
...selectSenderColumns]

const SelectTable = (props: Props) => {
const {sendRequest}=useHttp()
const [data,setData]=useState<Array<FileObj>|null>(null)

const [selectionModel,setSelectionModel]=useState<GridSelectionModel>([])



//fetching all available file to create row to our select table
useEffect(()=>{

const fetchAvailableFiles=async()=>{
  const host=window.location.hostname
const {fileList,portList} =await sendRequest(`http://${host}:8087/flow-analyzer/api/${props.fetchPath}`);
if(fileList){
  setData(fileList)
}
if(portList){
  setData([...portList.sflow,...portList.netflow])
}

}
if(props.selectedFiles){
  setSelectionModel(props.selectedFiles)
  }
  if(!data){
    fetchAvailableFiles()
  }



},[sendRequest,props.fetchPath,props.selectedFiles])


//sending the names of the selected files and getting the summed fileArray with all the rows of the files
const submitHandler=async(e:any)=>{
e.preventDefault()
const filesList=data.filter((file:FileObj)=>(selectionModel.includes(file.filename)))
props.submitHandler(filesList)
}

  return (
    <div  style={{minHeight:"300px",height:"70vh", width:'70vw',display:'flex',flexDirection:'column', justifyContent:'center',alignItems:'center'}} >
    {data&&<DataGrid  initialState={{
    sorting: {
      sortModel: [{ field: 'date', sort: 'desc' }],
    },
  }} isRowSelectable={(params)=>(props.senders?!selectionModel.length||selectionModel.includes(params.id):true)} rowHeight={props.senders? 45 :25} sx={{width:'100%',}} checkboxSelection={true} columns={props.senders?columns:selectFileColumns} rows={data} getRowId={(row)=>(row.filename)}  onSelectionModelChange={(newSelectionModel) => {      
      
    setSelectionModel(newSelectionModel);
        }}
        selectionModel={selectionModel}/>}
        <button  className='analyze-btn' disabled={!selectionModel.length} onClick={submitHandler}>{props.senders?'Select Sender':"Analyze"}</button>
    </div>
  )
}

export default SelectTable