import { useEffect, useMemo, useState } from "react"
import { useHttp } from "./http-hook"

type FileObj= {
  ipProtocol:string,
  ipSender:string,
  isSflow:boolean
}

export const useFileInit=(file:Array<FileObj>)=>{
    const [portList,setPortList]=useState<any>(null)
   
    const {sendRequest}=useHttp();
  
    // initail creation of protocolArray based on current file protocols
   const {senderArray,portArray,protocolArray}=useMemo(() => {
        let protocolArray:any=[];
    let senderArray:any=[];
    let portArray:any=[]
        if(file){
            protocolArray=(file.reduce((accumulator:any, currentValue:any) => {
                if (!accumulator.includes(currentValue.ipProtocol) && currentValue.ipProtocol !== undefined) {
                  return [...accumulator, `${currentValue.ipProtocol}`];
                }
                return accumulator;
            
              }, []))
            
              senderArray=(file.reduce((accumulator:any, currentValue:any) => {
                if (!accumulator.includes(currentValue.ipSender) && currentValue.ipSender !== undefined) {
                  return [...accumulator, `${currentValue.ipSender}`];
                }
                return accumulator;
            
              }, []))
            
              portArray=file.reduce((accumulator:any, currentValue:any) => {
                if (!accumulator.includes(currentValue.outputPort1) && currentValue.outputPort1 !== undefined) {
                  return [...accumulator, `${currentValue.outputPort1}`];
                }
                return accumulator;
            
              }, [])
      
              portArray.concat(file.reduce((accumulator:any, currentValue:any) => {
                if (!portArray.includes(currentValue.outputPort2) && currentValue.outputPort2 !== undefined) {
                  return [...accumulator, `${currentValue.outputPort2}`];
                }
                return accumulator;
            
              }, []))
            }
return{portArray,senderArray,protocolArray }

    },[file])
      


//fetch interfaces on file load and sender array creation 
useEffect(()=>{
    if(file&&senderArray){
 const fetchPortList= async(senderArr:Array<string>)=> {
 
const sendersSflow:Array<string>=file.reduce((acc:Array<string>,cur:FileObj)=>{
  if(!acc.includes(cur.ipSender)&&cur.isSflow){
    return [...acc,cur.ipSender]
  }
else return [...acc]
},[])

const sendersNetflow=senderArr.filter((sender)=>!sendersSflow.includes(sender))
const host=window.location.hostname
  const {portList}=await sendRequest(`http://${host}:8087/flow-analyzer/api/intf`,'POST',JSON.stringify({
    senderList: {sflow:sendersSflow,netflow:sendersNetflow},
  }),
  {
    "Content-Type": "application/json",
  })
    setPortList(portList)
    }
fetchPortList(senderArray)
}
  },[file,sendRequest,senderArray])





return {portList,portArray,protocolArray,senderArray,setPortList}

}