import React ,{useState,useContext,useEffect} from "react"
import { FileCtx } from '../../ctx/file-ctx';
import {FormControl,InputLabel,Select,MenuItem} from '@mui/material'
import styles from './Filter.module.css'
import { protocolIndex } from "../../util/ipProtocols";

type Props = {onFilter:Function,sender:string,port:string,protocol:string,}

const Filter = (props: Props) => {
  let {file,protocolArray,senderArray,portArray,portList,selectedSender}=useContext(FileCtx)
    const [sender,setSender]=useState<any>("504")
    const [port,setPort]=useState<any>("abcd")
    const [protocol,setProtocol]=useState<any>('504')
    const [isOpen,setIsOpen]=useState<any>(false)
    const [portsArray,setPortsArray]=useState<any>(null)
    const [protocolsArray,setProtocolsArray]=useState<any>(null)



    //when port array initaited set the state rernder when portArray changes
useEffect(()=>{
    setProtocol(props.protocol)
    setPort(props.port)
    setSender(props.sender)
   
  if(portArray.length&&!portsArray&&props.sender!=='504'){
    if(props.sender!=='504'){
      setPortsArray(file.reduce((accumulator:any, currentValue:any) => {
        if (!accumulator.includes(currentValue.outputPort1) && currentValue.ipSender===props.sender && currentValue.outputPort1 !== undefined) {
          return [...accumulator, `${currentValue.outputPort1}`];
        }
        return accumulator;
      }, []))
    }else{
      setPortsArray(portArray)
    }
  }
    
      if( protocolArray.length&&!protocolsArray&&props.sender!=='504'){
       setProtocolsArray(file.reduce((accumulator:any, currentValue:any) => {
          if (!accumulator.includes(currentValue.ipProtocol) && currentValue.ipSender===props.sender && currentValue.ipProtocol !== undefined) {
            return [...accumulator, `${currentValue.ipProtocol}`];
          }
          return accumulator;
      
        }, []))
      }else{
        setProtocolsArray(protocolArray)
      }
      if(senderArray.length===1){
        setSender(senderArray[0])
      }
  
    },[props,portArray,protocolArray,senderArray,isOpen])



//sender change handler
const changeHandler=(e:any)=>{
    e.preventDefault()
     let ref=e.target.value
    setSender(ref)
    if(ref !== '504' && file   ){
      portArray=file.reduce((accumulator:any, currentValue:any) => {
        if (!accumulator.includes(currentValue.outputPort1) && currentValue.ipSender===ref && currentValue.outputPort1 !== undefined) {
          return [...accumulator, `${currentValue.outputPort1}`];
        }
        return accumulator;
    
      }, [])
      protocolArray=file.reduce((accumulator:any, currentValue:any) => {
        if (!accumulator.includes(currentValue.ipProtocol) && currentValue.ipSender===ref && currentValue.ipProtocol !== undefined) {
          return [...accumulator, `${currentValue.ipProtocol}`];
        }
        return accumulator;
    
      }, [])
      setPortsArray(portArray);
      setProtocolsArray(protocolArray)
      setPort('abcd')
      setProtocol('504')
    }
    else if(ref === '504' && file   ){
      portArray=file.reduce((accumulator:any, currentValue:any) => {
        if (!accumulator.includes(currentValue.outputPort1) &&  currentValue.outputPort1 !== undefined) {
          return [...accumulator, `${currentValue.outputPort1}`];
        }
        return accumulator;
    
      }, [])
      protocolArray=file.reduce((accumulator:any, currentValue:any) => {
        if (!accumulator.includes(currentValue.ipProtocol) &&  currentValue.ipProtocol !== undefined) {
          return [...accumulator, `${currentValue.ipProtocol}`];
        }
        return accumulator;
    
      }, [])
      setPortsArray(portArray);
      setProtocolsArray(protocolArray)
      
    }
     }
     
    //port change Handler
     const changePortHandler=(e:any)=>{
      e.preventDefault()
      setPort(e.target.value)
       }
    
       //protocol change Handler
     const changeProtocolHandler=(e:any)=>{
      e.preventDefault()
      setProtocol(e.target.value)

       }

       //filter btn Handler
       const onSubmitHandler=(e:any)=>{
e.preventDefault()
props.onFilter(sender,port,protocol)
setIsOpen(false)
       }




  return (
    <form className={styles.filter_tab} onSubmit={onSubmitHandler}>
        <FormControl   className={styles.filters}>
            <InputLabel sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} htmlFor="sender">Sender</InputLabel>
<Select sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} defaultValue={senderArray.length===1? senderArray[0] :(selectedSender??'504')} label="Sender" className={styles.select} onChange={changeHandler} id="sender" name="sender">
<MenuItem sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} disabled={senderArray.length<=1} value={'504'}>All</MenuItem>
 { senderArray.map((sender:string)=>{
  if(sender.trim() !== '' ){
  return  <MenuItem sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} key={sender} value={sender}>{sender +` (${portList[sender]?.senderName??"interface does not exist"})`}</MenuItem>
  }
  return ''
})}
</Select>
</FormControl>
        <FormControl  className={styles.filters}>
            <InputLabel sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} htmlFor="port">Interfaces</InputLabel>
<Select sx={{color:"rgba(57, 101, 125, 0.8274509804)"}}  value={port}  label="Port" className={styles.select} onChange={changePortHandler}   id="port" name="port">
<MenuItem sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} value={"abcd"}>All</MenuItem>
 {portList&&portsArray&& portsArray.map((port:string)=>{
  if(port.trim() !== '' && portList![sender] ){
    let trimmedPort;
    if(port.indexOf('00')=== 0){
      trimmedPort=port.replace('00',"")
    }else{
      trimmedPort=port
    }
  
 const portDescription=portList![sender].data.filter((row:Array<string>)=>{
return row[1]===trimmedPort
  })
  let text:any;
  if(portDescription.length){
    const portArr=portDescription[0]
    const description=portArr.slice(7).join("").replaceAll("-",'')
   const ip=portArr[6].replaceAll("-_ -_",'')
   text=` ${ip} ( ${description} )`
  }else{
    text=''
  }
  return  <MenuItem sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} key={port} value={port}>{port+' '+text}</MenuItem>
  }else if(port.trim() !== '' && !portList![sender]){
  return  <MenuItem sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} key={port} value={port}>{port+' '}</MenuItem>
 } 
 return ""
 })
}
 </Select>
   </FormControl>
        <FormControl className={styles.filters}>
        <InputLabel sx={{color:"rgba(57, 101, 125, 0.8274509804)"}}  htmlFor="protocol">Protocol</InputLabel>
  <Select sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} defaultValue={protocol ? protocol: '504'}   label="Protocol"  className={styles.select} onChange={changeProtocolHandler} id="protocol" name="protocol">
  <MenuItem sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} value={"504"}>All</MenuItem>
 {protocolsArray&& protocolsArray.map((protocol:string)=>{
  if(protocol.trim() !== '' ){
  return  <MenuItem sx={{color:"rgba(57, 101, 125, 0.8274509804)"}} key={protocol} value={protocol}>{protocol+' '+protocolIndex[parseInt(protocol)][0]}</MenuItem>
  }
  return ''
  })
   }
  </Select>
        </FormControl>       
         <div className={styles.filter_btn}>
            <button type='submit'>filter</button>
         </div>
</form>

  )
}

export default Filter